@import "../../../../base";

.select {
  width: 25rem;
  position: relative;

  &::after {
    content: "\25BC";
    position: absolute;
    top: 62%;
    right: 0;
    padding: 0 2rem;
    background: $color-primary;
    cursor: pointer;
    pointer-events: none;
    transition: 0.25s all ease;
    color: #fff;
    font-size: 1.7rem;
  }
  &:hover::after {
    color: $color-secondary;
  }
  @media only screen and (max-width: $bp-medium) {
    width: 50%;
  }
}

select {
  padding: 1.5rem;
  width: 100%;
  background-color: $color-primary;
  color: #fff;
  appearance: none;
  border: none;
  outline: none;
  font-family: inherit;
}

.price {
  @media only screen and (max-width: $bp-medium) {
    width: 50%;
  }
}

#price {
  appearance: none;
  width: 25rem;
  height: 0.7rem;
  outline: none;
  border-radius: 0.25rem;
  background-color: $color-grey-light-2;

  &::-webkit-slider-thumb {
    appearance: none;
    width: 2.5rem;
    height: 2.5rem;
    background-color: #fff;
    border: 0.4rem solid $color-primary;
    border-radius: 10rem;
    cursor: pointer;
  }
  &::-moz-range-thumb {
    appearance: none;
    width: 2.5rem;
    height: 2.5rem;
    background-color: #fff;
    border: 0.4rem solid $color-primary;
    border-radius: 10rem;
    cursor: pointer;
  }
  @media only screen and (max-width: $bp-medium) {
    width: 100%;
  }
}

.input__size {
  display: flex;
  align-items: flex-end;
  width: 25rem;
  justify-content: space-between;

  & input {
    width: 100%;
    background-color: #fff;
    border: 1px solid $color-primary;
    padding: 10px 12px;
    text-align: center;
    outline: none;
  }
  @media only screen and (max-width: $bp-medium) {
    width: 50%;
  }
}

label {
  font-size: 2rem;
  display: block;
  margin-bottom: 2rem;
}
