@import "../../base";

.property-details {
  position: relative;
  z-index: 100;
  background-color: #fff;
 
  display: grid;
  grid-template-columns:
    [full-start] minmax(6rem, 1fr) [center-start] repeat(
      8,
      [col-start] minmax(min-content, 14rem) [col-end]
    )
    [center-end] minmax(6rem, 1fr) [full-end];

    margin-bottom: 4rem;
    margin-bottom: 28rem;

  &__gallery {
    margin-top: 30rem;
    background-color: $color-grey-light-1;
    grid-column: full-start / full-end;
    padding: 5rem 1.2rem;
    display: grid;
    justify-content: center;
    grid-template-columns: 80vw;

    & figure {
      margin-bottom: 1rem;
    }
    .room-item img {
      width: 100%;
      object-fit: cover;
      display: block;
    }
    .slick-thumb {
      bottom: -4.5rem;
    }
    .slick-dots {
        & li {
            width: 8rem;
             height: 6rem;
             margin: 0 1rem;
        }
        & li  img {
          height: 100%;
          filter: grayscale(100%);
        }
        & li.slick-active  img {
          height: 100%;
          filter: grayscale(0);
        }
    }
  }
  &__info {
    grid-column: center-start / center-end;
    padding-bottom: 4rem;
    &:after {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      bottom: -25px;
      left: 50%;
      margin-left: -25px;
      background: #fff;
      transform: rotate(45deg);
    }
  }

  @media only screen and (max-width: $bp-medium) {
    margin-bottom: 38rem;
  }
  @media only screen and (max-width: $bp-small) {
    margin-bottom: 0;
  }
}
