@import '../../../../base';

.tabs  {
    background-color: #fff;
    color: $color-primary;

    &__info--box {
        display: grid;
        grid-template-columns: repeat(2, minmax(2rem, 1fr));
        justify-items: start;
        column-gap: 5rem;
        row-gap: 3rem;
        &-details {
            letter-spacing: .2rem;
        }
    }
}

.panel-padding {
    padding-top: 2.5rem;
}