
.property {
    grid-column: center-start / center-end;
    margin: 15rem 0;
  
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    gap: 7rem;

    &__error {
      font-size: 3rem;
      text-align: center;
      font-weight: 700;
    }

    &:after {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      bottom: -25px;
      left: 50%;
      margin-left: -25px;
      background: #fff;
      transform: rotate(45deg);
    }

  }